import { InputLabel, MenuItem, Select, SelectChangeEvent } from "@rp/react/web/mui/core"
import { useFeedbackContext } from "./FeedbackContext"
import { FeedbackForm, FormControlStyled } from "./FeedbackMode.styles"
import { UserTypes, userTypeOptions } from "./feedback.types"

export default function PersonaTypeForm() {
  const { userType, setUserType } = useFeedbackContext()

  const handleChange = (event: SelectChangeEvent) => {
    setUserType(event.target.value as UserTypes)
  }

  const options = userTypeOptions.map(option => (
    <MenuItem key={option.value} value={option.value}>
      {option.label}
    </MenuItem>
  ))

  return (
    <FeedbackForm>
      <FormControlStyled>
        <InputLabel id="persona-type-label">Persona type</InputLabel>
        <Select
          disabled
          value={userType}
          label="persona-type"
          onChange={handleChange}
          inputProps={{
            "data-testid": "user-type-options-id",
          }}
        >
          {options}
        </Select>
      </FormControlStyled>
    </FeedbackForm>
  )
}
